import React from 'react'

// material-ui
import Box from '@material-ui/core/Box'
import Paper from '@material-ui/core/Paper'

// custom component
import SiteHeader from '../components/Headers/SiteHeader'
import GridList from '../components/Lists/GridList'

// projects data query
import useProjects from '../data/useProjects'

/**
 * Opinion
 * @param {Object} props - component properties
 * @param {Object} props.location - representing where the app is from a router perspective
 * @returns {*}
 * @constructor
 */
function OpinionPage({ location }) {
  const projects = useProjects()
  return (
    <SiteHeader title="Home of modern software development" location={location}>
      <Box clone pt={16} pr={2} pb={2} pl={2}>
        <Paper elevation={0}>
          <GridList edges={projects} location={location} />
        </Paper>
      </Box>
    </SiteHeader>
  )
}

export default OpinionPage
