import { useStaticQuery, graphql } from 'gatsby'

/**
 * Projects data query
 */
export default function useProjects() {
  const {
    allMarkdownRemark: { edges: services }
  } = useStaticQuery(graphql`
    query allPublishedProjects {
      allMarkdownRemark(
        filter: {
          fileAbsolutePath: { regex: "/(projects)/.*\\\\.md$/" }
          frontmatter: { published: { ne: "No" } }
        }
        sort: { fields: [frontmatter___date], order: DESC }
      ) {
        edges {
          node {
            excerpt(pruneLength: 180)
            timeToRead
            fields {
              slug
            }
            frontmatter {
              title
              subtitle
              date(formatString: "YYYY-MM-DD")
              publishedBy
              image {
                publicURL
              }
            }
          }
        }
      }
    }
  `)

  return services
}
